import { FarmingImage } from '@/components/common/FarmingIcon'
import { MiningImage } from '@/components/common/MiningIcon'
import usePositionListAction from '@/hooks/position/usePositionListAction'
import usePositionStore from '@/store/position'
import { DlmmPosBaseInfo } from '@/types/dlmm'
import { PosBaseInfo, showNewVersionApr } from '@/types/position'
import { Block, TableSortTh } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import useTokenPriceStore from '@cetus/stores/src/tokenPrice'
import { d, isAvailableObject } from '@cetus/utils'
import { HStack, Skeleton, Text, VStack } from '@chakra-ui/react'
import { useDebounceEffect } from 'ahooks'
import { useCallback, useMemo, useState } from 'react'
import PositionItemContent from '../../common/PositionItemContent'
const itemWidth = ['320px', '80px', '170px', '140px', '180px', '224px']

const SORT_BY_LIST = [
  { label: 'APR', value: 'apr' },
  { label: 'Liquidity', value: 'liquidity' },
  { label: 'Pending Fees', value: 'fees' },
  { label: 'Pending Rewards', value: 'rewards' },
  { label: 'Actions', value: 'actions' }
]

const SORT_BY_OBJECT = SORT_BY_LIST.reduce((obj: any, item) => {
  obj[item.value] = item
  return obj
}, {})

function PositionItem({ poolInfo, priceDirect }: { poolInfo: any; priceDirect?: boolean }) {
  const itemWidth = showNewVersionApr
    ? ['380px', '100px', '130px', '170px', '160px', '106px']
    : ['300px', '100px', '110px', '140px', '180px', '224px']
  console.log('ðŸš€ðŸš€ðŸš€ ~ PositionItem.tsx:16 ~ PositionItem ~ showNewVersionApr:', showNewVersionApr)

  const SORT_BY_LIST = showNewVersionApr
    ? [
        { label: 'APR', value: 'apr' },
        { label: 'Liquidity', value: 'liquidity' },
        { label: 'Claimable Yield', value: 'yield' },
        { label: 'Est. Daily Yield', value: 'dailyEarn' },
        // { label: 'Claimable Fees', value: 'fees' },
        // { label: 'Pending Rewards', value: 'rewards' },
        { label: 'Actions', value: 'actions' }
      ]
    : [
        { label: 'APR', value: 'apr' },
        { label: 'Liquidity', value: 'liquidity' },
        { label: 'Pending Fees', value: 'fees' },
        { label: 'Pending Rewards', value: 'rewards' },
        { label: 'Actions', value: 'actions' }
      ]

  const SORT_BY_OBJECT = SORT_BY_LIST.reduce((obj: any, item) => {
    obj[item.value] = item
    return obj
  }, {})
  console.log('ðŸš€ðŸš€ðŸš€ ~ PositionItem.tsx:38 ~ PositionItem ~ SORT_BY_OBJECT:', SORT_BY_OBJECT)
  const { coinPriceObj } = useTokenPriceStore()
  const { getPositionSortList } = usePositionListAction()
  const { farmsPosRewardsData, posRewardsData } = usePositionStore()

  const [sortRule, setSortRule] = useState<'asc' | 'desc'>('desc')
  const [sortBy, setSortBy] = useState<{ label: string; value: string }>({ label: '', value: '' })

  const handleClickSort = (item: any) => {
    if (item?.value === sortBy.value) {
      setSortRule(prev => (prev === 'desc' ? 'asc' : 'desc'))
    } else {
      setSortRule('desc')
      setSortBy(item)
    }
  }
  const [sortList, setSortList] = useState<(PosBaseInfo | DlmmPosBaseInfo)[]>([])
  const [listLoading, setListLoading] = useState<boolean>(true)
  const [positionList, setPositionList] = useState<(PosBaseInfo | DlmmPosBaseInfo)[]>([])
  const handleGetPositionList = useCallback(async () => {
    setListLoading(true)
    try {
      console.log(sortBy.value, sortRule, poolInfo.list, 'handleGetPositionList')
      const list = await getPositionSortList(sortBy.value, sortRule, poolInfo.list)
      setSortList(list)
      setPositionList(list)
      setListLoading(false)
    } catch (error) {
      console.log(error, 'handleGetPositionList')
      setListLoading(false)
    }
  }, [sortBy.value, sortRule, poolInfo.list, getPositionSortList])
  useDebounceEffect(
    () => {
      if (isAvailableObject(poolInfo)) {
        handleGetPositionList()
      }
    },
    [handleGetPositionList],
    { wait: 300 }
  )

  const hasPositiveAmount = (key: 'totalMiningAmount' | 'totalFarmingAmount') => sortList.some((item: any) => d(item[key]).gt(0))

  const showMiningIcon = useMemo(() => hasPositiveAmount('totalMiningAmount'), [sortList])
  const showFarmingIcon = useMemo(() => hasPositiveAmount('totalFarmingAmount'), [sortList])

  const { isApp } = useWindowWidth()

  return (
    <VStack gap="12px" w="100%">
      {!isApp && (
        <HStack w="100%" p="0 16px" justify="space-between">
          <Text w={itemWidth[0]} minW={{ base: 'unset', lg: itemWidth[0] }}>
            Price Range
          </Text>
          {SORT_BY_LIST.map((item, idx) => {
            if (item.value !== 'rewards' || showMiningIcon || showFarmingIcon) {
              return (
                <HStack key={item.value} gap="4px" w={itemWidth[idx + 1]} justify="flex-end">
                  {!showNewVersionApr && showMiningIcon && item.value === 'rewards' && <MiningImage />}
                  {!showNewVersionApr && showFarmingIcon && item.value === 'rewards' && <FarmingImage />}
                  {showNewVersionApr && showMiningIcon && item.value === 'yield' && <MiningImage />}
                  {showNewVersionApr && showFarmingIcon && item.value === 'yield' && <FarmingImage />}
                  {item.value == 'actions' && <Text>{item.label}</Text>}
                  {item.value === 'apr' && showNewVersionApr && (
                    <TableSortTh
                      labelInfo={SORT_BY_OBJECT[item.value]}
                      sortRule={sortRule}
                      sortBy={sortBy}
                      clickSort={handleClickSort}
                      defaultShowSortIcon={true}
                      tooltip={{
                        content:
                          item.value === 'apr' ? (
                            <span>
                              APR based on the daily yield accrued by this position. Past performance is not indicative of future results.
                              Calculations are an estimate and only for reference.
                            </span>
                          ) : (
                            ''
                          )
                      }}
                    />
                  )}
                  {item.value !== 'apr' && item.value !== 'actions' && (
                    <TableSortTh
                      labelInfo={SORT_BY_OBJECT[item.value]}
                      sortRule={sortRule}
                      sortBy={sortBy}
                      clickSort={handleClickSort}
                      defaultShowSortIcon={true}
                      tooltip={{
                        content:
                          item.value === 'apr' ? (
                            <span>
                              APR based on the daily yield accrued by this position. Past performance is not indicative of future results.
                              Calculations are an estimate and only for reference.
                            </span>
                          ) : (
                            ''
                          )
                      }}
                    />
                  )}
                </HStack>
              )
            }
            return null
          })}
        </HStack>
      )}

      <VStack w="100%" maxH="440px" overflow="auto" gap="12px">
        {listLoading && positionList?.length === 0
          ? poolInfo?.list?.map((item, index) => (
              <Block w="100%" key={index} bg="position_bg" border="none" p={{ base: '16px 8px 12px', lg: '20px 16px ' }} borderRadius="16px">
                <HStack w="100%" justify="space-between" flexDirection={{ base: 'column', lg: 'row' }}>
                  <VStack w="100%" align="flex-start" gap="14px">
                    {isApp && (
                      <HStack flexDirection={{ base: 'column', lg: 'row' }} w={{ base: '100%', lg: 'unset' }} gap="12px">
                        <HStack w={{ base: '100%', lg: 'unset' }} justify={{ base: 'space-between', lg: 'flex-start' }}>
                          <Text color="primary_gray" fontSize={{ base: '14px', lg: '12px' }}>
                            NFT ID
                          </Text>
                          <Skeleton isLoaded={false} h="14px" />
                        </HStack>
                        <HStack w={{ base: '100%', lg: 'unset' }} justify={{ base: 'space-between', lg: 'flex-start' }}>
                          <Text color="primary_gray" fontSize={{ base: '14px', lg: '12px' }} ml={{ base: '0', lg: '8px' }}>
                            Address
                          </Text>
                          <Skeleton isLoaded={false} h="14px" />
                        </HStack>
                      </HStack>
                    )}
                    <HStack w="100%" justify="space-between" flexDirection={{ base: 'column', lg: 'row' }} gap={{ base: '14px', lg: '8px' }}>
                      <HStack
                        minW={{ base: 'unset', lg: itemWidth[0] }}
                        w={{ base: '100%', lg: itemWidth[0] }}
                        justify={{ base: 'space-between', lg: 'flex-start' }}
                      >
                        {isApp && (
                          <Text color="primary_gray" whiteSpace="nowrap">
                            Price Range
                          </Text>
                        )}
                        <Skeleton isLoaded={false} h="14px" w={{ base: '96px', lg: '40%' }} />
                      </HStack>
                      {
                        <HStack w={{ base: '100%', lg: itemWidth[1] }} justify={{ base: 'space-between', lg: 'flex-end' }}>
                          {isApp && showNewVersionApr && (
                            <HStack gap="2px">
                              <Text color="primary_gray">APR</Text>
                            </HStack>
                          )}
                          {showNewVersionApr && <Skeleton isLoaded={false} h="14px" />}
                        </HStack>
                      }
                      <HStack w={{ base: '100%', lg: itemWidth[2] }} justify={{ base: 'space-between', lg: 'flex-end' }}>
                        {isApp && <Text color="primary_gray">Liquidity</Text>}
                        <Skeleton isLoaded={false} h="14px" />
                      </HStack>

                      {showNewVersionApr && (
                        <HStack w={{ base: '100%', lg: itemWidth[3] }} justify={{ base: 'space-between', lg: 'flex-end' }}>
                          {isApp && <Text color="primary_gray">Claimable Yield</Text>}
                          <Skeleton isLoaded={false} h="14px" />
                        </HStack>
                      )}
                      {showNewVersionApr && (
                        <HStack w={{ base: '100%', lg: itemWidth[4] }} justify={{ base: 'space-between', lg: 'flex-end' }}>
                          {isApp && <Text color="primary_gray">Est. Daily Yield</Text>}
                          <Skeleton isLoaded={false} h="14px" />
                        </HStack>
                      )}
                      {!showNewVersionApr && (
                        <HStack m="-12px 0" w={{ base: '100%', lg: itemWidth[3] }} justify={{ base: 'space-between', lg: 'flex-end' }}>
                          {isApp && <Text color="primary_gray">Pending Fees</Text>}
                          <Skeleton isLoaded={false} h="14px" />
                        </HStack>
                      )}

                      {!showNewVersionApr && (showMiningIcon || showFarmingIcon) && (
                        <HStack m="-12px 0" w={{ base: '100%', lg: itemWidth[4] }} justify={{ base: 'space-between', lg: 'flex-end' }}>
                          {isApp && <Text color="primary_gray">Pending Rewards</Text>}
                          <HStack gap={{ base: '8px', lg: '12px' }}>
                            {isApp && showMiningIcon && <MiningImage />}
                            {isApp && showFarmingIcon && <FarmingImage />}
                            <Skeleton isLoaded={false} h="14px" />
                          </HStack>
                        </HStack>
                      )}
                      <HStack w={{ base: '100%', lg: itemWidth[5] }} justify={{ base: 'space-between', lg: 'flex-end' }}>
                        <Skeleton isLoaded={false} h={{ base: '20px', lg: '14px' }} w={{ base: '100%', lg: '96px' }} />
                      </HStack>
                    </HStack>
                  </VStack>
                </HStack>
              </Block>
            ))
          : positionList.map((item: any) => (
              <PositionItemContent
                poolType="clmm"
                key={item?.id}
                positionInfo={item}
                positionItemWidth={itemWidth}
                priceDirect={priceDirect}
                showMiningIcon={showMiningIcon}
                showFarmingIcon={showFarmingIcon && (!isApp || item.posType == 'farms')}
              />
            ))}
      </VStack>
    </VStack>
  )
}

export default PositionItem
